  .n-input-container.with-label .n-input-wrapper.has-input .n-input textarea {
    padding-top: 22px;
    padding-bottom: 0px;
    font-size: 1.05rem; }
  .n-input-container.solo .n-input-wrapper {
    height: 54px; }
    .n-input-container.solo .n-input-wrapper .n-input {
      height: 54px; }
      .n-input-container.solo .n-input-wrapper .n-input input,
      .n-input-container.solo .n-input-wrapper .n-input select,
      .n-input-container.solo .n-input-wrapper .n-input textarea {
        height: 53px; }
  .n-input-container.solo.compact .n-input-wrapper {
    height: 36px; }
    .n-input-container.solo.compact .n-input-wrapper .n-input {
      height: 36px; }
      .n-input-container.solo.compact .n-input-wrapper .n-input input,
      .n-input-container.solo.compact .n-input-wrapper .n-input select,
      .n-input-container.solo.compact .n-input-wrapper .n-input textarea {
        height: 35px;
        font-size: 1rem; }
  .n-input-container .n-input-wrapper {
    min-height: 54px;
    display: flex;
    width: 100%;
    min-width: 50px;
    max-width: 100%;
    flex-direction: row;
    align-items: center;
    flex-grow: 1;
    flex-shrink: 1;
    transition: all 0.2s ease-in-out;
    background-color: var(--color-solid-half);
    border-radius: 12px;
    border: solid 1px var(--color-faded-1); }
    .n-input-container .n-input-wrapper.has-focus {
      box-shadow: 0px 3px 7px -2px rgba(49, 158, 215, 0.1);
      background-color: var(--input-focus-background); }
    .n-input-container .n-input-wrapper textarea {
      border: none; }
    .n-input-container .n-input-wrapper .n-input {
      transition: all 0.2s ease-in-out;
      display: flex;
      flex-direction: column;
      flex-grow: 1;
      flex-shrink: 1;
      color: var(--color-inverse); }
      .n-input-container .n-input-wrapper .n-input label {
        transition: all 0.2s ease-in-out;
        opacity: 0;
        transform: translateY(20px);
        pointer-events: none;
        font-size: 0.7rem;
        position: absolute;
        top: 8px;
        left: 10px;
        margin: 0;
        padding: 0; }
      .n-input-container .n-input-wrapper .n-input input,
      .n-input-container .n-input-wrapper .n-input select,
      .n-input-container .n-input-wrapper .n-input textarea {
        width: 100%;
        transition: all 0.2s ease-in-out;
        margin: 0;
        padding-left: 8pt;
        padding-right: 8pt;
        background-color: transparent !important;
        outline: none; }
        .n-input-container .n-input-wrapper .n-input input:disabled,
        .n-input-container .n-input-wrapper .n-input select:disabled,
        .n-input-container .n-input-wrapper .n-input textarea:disabled {
          background-color: transparent !important;
          color: var(--color-inverse-2); }
      .n-input-container .n-input-wrapper .n-input textarea {
        min-height: 90px;
        padding-top: 8pt;
        color: var(--color-inverse); }
</style>

<div
  class="n-input-container {className}
  {compact ? 'compact' : ''}
  {solo ? 'solo' : 'with-label'}"
  style="{width ? `max-width:${width}; width:${width}; ` : ``}
  {style}">
  <div
    class="n-input-wrapper {hasInput ? 'has-input' : 'no-input'}
    {focused ? 'has-focus' : 'no-focus'}">
    <slot name="left" />
    <div class="n-input">
      <label>{label || placeholder}</label>
      {#if type == 'email'}
        <input
          bind:this={_elInput}
          {disabled}
          {inputmode}
          type="email"
          style={inputStyle}
          class={inputClass}
          {pattern}
          bind:value
          {autocomplete}
          {autocorrect}
          {autocapitalize}
          {placeholder}
          on:keyup={change}
          on:focus={focus}
          on:blur={blur} />
      {:else if type == 'password'}
        <input
          bind:this={_elInput}
          {disabled}
          {inputmode}
          type="password"
          style={inputStyle}
          class={inputClass}
          {pattern}
          bind:value
          {autocomplete}
          {autocorrect}
          {autocapitalize}
          {placeholder}
          on:keyup={change}
          on:focus={focus}
          on:blur={blur} />
      {:else if type == 'number'}
        <input
          bind:this={_elInput}
          {disabled}
          {inputmode}
          type="number"
          style={inputStyle}
          class={inputClass}
          bind:value
          {autocomplete}
          {autocorrect}
          {autocapitalize}
          {placeholder}
          on:keyup={change}
          on:focus={focus}
          on:blur={blur} />
      {:else if type == 'select'}
        <select bind:this={_elInput} {disabled} on:change={change} bind:value>
          <slot />
        </select>
      {:else if type == 'textarea'}
        <textarea
          bind:this={_elInput}
          {disabled}
          {rows}
          style={inputStyle}
          class={inputClass}
          {autocomplete}
          {autocorrect}
          {autocapitalize}
          {placeholder}
          on:keyup={change}
          on:focus={focus}
          on:blur={blur}
          bind:value />
        <slot />
      {:else}
        <input
          bind:this={_elInput}
          {disabled}
          type="text"
          {inputmode}
          style={inputStyle}
          class={inputClass}
          {pattern}
          bind:value
          {autocomplete}
          {autocorrect}
          {autocapitalize}
          {placeholder}
          on:keyup={change}
          on:focus={focus}
          on:blur={blur} />
      {/if}
    </div>
    <slot name="right">
      {#if type == 'select'}
        <button class="btn btn-clear select-arrow">
          <NIcon name="chevronDown" />
        </button>
      {/if}
    </slot>

  </div>
  {#if help}
    <div class="helper">{help}</div>
  {/if}
</div>
